//Import necessary dependencies

import React from "react"
import {
    TransitionGroup,
    Transition as ReactTransition,
} from "react-transition-group"

//This variable will be responsible for our animation duration
const timeout = 400

//This object contains basic styles for animation, but you can extend them to whatever you like. Be creative!
const getTransitionStylesLeft = {
    entering: {
        position: 'absolute',
        // opacity: 0,
        transform: "translateX(100%)  scale(1)"
    },
    entered: {
        transition: `all ${timeout}ms ease-in-out`,
        opacity: 1,
        transform: "translateX(0px)  scale(1)"
    },
    exiting: {
        transition: `all ${timeout}ms ease-in-out`,
        transform: "translateX(-100%)  scale(1)",
        opacity: 0
    },
}

const getTransitionStylesRight = {
    entering: {
        position: 'absolute',
        // opacity: 0,
        transform: "translateX(-100%)  scale(1)"
    },
    entered: {
        transition: `all ${timeout}ms ease-in-out`,
        // opacity: 1,
        transform: "translateX(0px)  scale(1)"
    },
    exiting: {
        transition: `all ${timeout}ms ease-in-out`,
        transform: "translateX(100%)  scale(1)",
        // opacity: 0
    },
}

class Transition extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            direction: true
        }

        this.childFactory = this.childFactory.bind(this);
    }
    componentWillReceiveProps(nextProps) {
        let paths = ["/", "/r1t/", "/r1s/", "/technology/", "/careers/", "/about/"]
        let current = paths.indexOf(this.props.location.pathname);
        let next = paths.indexOf(nextProps.location.pathname);

        this.setState({
            direction: current > next
        })
    }

    childFactory(child) {
        // return React.cloneElement(child, {
        //     transitionState,
        //     dispatch
        // })
    }


    render() {
        //Destructuring props to avoid garbage this.props... in return statement
        const { children, location } = this.props
        const style = this.state.direction ? getTransitionStylesRight : getTransitionStylesLeft

        console.log("STATE UPDATED", this.state.direction ? "Left" : "Right")

        return (
            //Using TransitionGroup and ReactTransition which are both
            //coming from  'react-transition-group' and are required for transitions to work
            <TransitionGroup >
                <ReactTransition
                //the key is necessary here because our ReactTransition needs to know when pages are entering/exiting the DOM
                    key={location ? location.pathname : Math.random()}
                    //duration of transition
                    timeout={{
                        enter: timeout,
                        exit: timeout,
                    }}
                >
                    {
                        //Application of the styles depending on the status of page(entering, exiting, entered) in the DOM
                        status => (
                        <div
                            style={{
                                ...style[status],
                            }}
                        >
                            {children}
                        </div>
                    )}
                </ReactTransition>
            </TransitionGroup>
        )
    }
}

export default Transition
